<template>
  <Loading
    :class="{
      'content--hide': !showLoading,
      'content--show': showLoading,
    }"
    v-if="displayLoading"
  />
  <div
    :class="{
      'markdown-content': true,
      'content--hide': !showContent,
      'content--show': showContent,
    }"
    v-if="displayContent"
  >
    <div class="markdown-body" v-html="htmlContent"></div>
  </div>
</template>

<script>
import { ref, onMounted } from "vue";
import md from "markdown-it";
import md_copy from "../utils/mdCopy";
import hljs from "highlight.js";
import Loading from "../components/Loading";
import data from "!raw-loader!../assets/md/TS.md";

let showLoading = ref(true);
let showContent = ref(false);
let displayLoading = ref(true);
let displayContent = ref(false);
const htmlContent = ref("");

export default {
  name: "TSNote",
  components: { Loading },
  setup() {
    onMounted(async () => {
      if (showLoading.value) {
        const mdConfig = md({
          html: true,
          linkify: true,
          typographer: true,
          // 針對特定語言顯示文字突出效果
          highlight (str, lang) {
            return hljs.highlight(str, {language: lang}).value;
          },
        }).use(md_copy);
        const result = mdConfig.render(`${data}`);
        htmlContent.value = result;

        setTimeout(() => {
          // 等待 v-if 效果
          showLoading.value = false;
          setTimeout(() => {
            // 等待loading動畫結束
            displayLoading.value = false;
            displayContent.value = true;
            setTimeout(() => {
              // 等待 v-if 效果
              showContent.value = true;
            }, 100);
          }, 500);
        }, 1000);
      }
    });
    return {
      showLoading,
      showContent,
      displayLoading,
      displayContent,
      htmlContent,
    };
  },
};
</script>
<style lang="scss">
@import "../style/md.scss";
.content--hide {
  opacity: 0;
}

.content--show {
  opacity: 1;
}
</style>
